html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  letter-spacing: .02em;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 26px;
}

body h1 {
  letter-spacing: .05em;
  color: #fff;
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 30px;
  font-style: normal;
  font-weight: bold;
  line-height: 48px;
}

@media screen and (min-width: 480px) {
  body h1 {
    font-size: 48px;
  }
}

body h2 {
  letter-spacing: .05em;
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 42px;
  font-style: normal;
  font-weight: bold;
  line-height: 49px;
}

body h3, body h4 {
  margin: 0;
}

body a {
  text-decoration: none;
}

body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

body .title {
  letter-spacing: .1em;
  text-transform: uppercase;
  align-items: center;
  margin: 0 0 20px;
  font-family: Open Sans, sans-serif;
  font-size: 11px;
  font-style: normal;
  font-weight: 600;
  line-height: 15px;
  display: inline-flex;
}

body .title:before {
  content: "";
  background-color: #303030;
  width: 60px;
  height: 1px;
  margin-right: 20px;
  display: inline-block;
}

body .button {
  text-align: center;
  letter-spacing: .09em;
  text-transform: uppercase;
  background-color: #0000;
  border: 1px solid #ff6c00;
  border-radius: 25px;
  width: 160px;
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 12px;
  line-height: 16px;
}

body .button:hover {
  cursor: pointer;
  color: #fff;
  background-color: #ff6c00;
}

body .container {
  width: 100%;
  margin: auto;
  padding-left: 10px;
  padding-right: 10px;
}

@media screen and (min-width: 480px) {
  body .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  body .container {
    width: 768px;
  }
}

